@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');
*{
  font-family: 'Open Sans', sans-serif;
}
body{
  font-size: 18px;
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
}
.App{
  max-width: 750px;
  width: 90%;
  margin: 0 auto 50px auto;
}
a:link, a:visited{
  text-decoration: none;
  color: black;
  transition: color 0.25s;
}
a:hover, a:active{
  color: #0085a1;
}
nav ul{
  list-style: none;
}
nav{
  display: grid;
  grid-template-columns: auto 1fr auto;
}
nav h3{
  grid-column: 1/2;
}
nav h3 a{
  font-size: 150%;
}
nav ul{
  grid-column: 3/4;
  padding-top: 10px;
}
nav ul li{
  padding: 25px;
  display: inline;
  font-weight: bold;
}
.articles>h1{
  text-align: center;
  margin-bottom: 50px;
}
.articles>h3{
  text-align: center;
}
.articles a:hover{
  color: #0085a1;
}
.articles a:after {
  display: block;
  height: 2px;
  background-color: rgba(0,0,0,.1);
  content: " ";
  width: 100%;
  margin: 0 auto;
  margin-top: 15px;
}
.articles>h1:after {
  display: block;
  height: 2px;
  background-color: black;
  content: " ";
  width: 100px;
  margin: 0 auto;
  margin-top: 10px;
}
.form>h1{
  text-align: center;
}
.form>div>label{
  display: grid;
  grid-template-columns: 100%;
  margin-bottom: 10px;
}
.error.hidden, .submitted.hidden{
  display: none;
}
.error.show{
  font-size: 0.85rem;
  display: block;
  color: red;
}
.submitted.show{
  font-size: 0.85rem;
  display: block;
  color: green;
}
input, textarea{
  font-size: 1.25rem;
  width: 100%;
  padding: 7px;
  border-radius: 3px;
  border: 1px solid transparent;
  border-bottom: 1px solid rgba(0,0,0,.1);
}
button {
  border: 0;
  background-color: #0085a1;
  display: block;
  padding: 12px 30px;
  text-decoration: none;
  border-radius: 2px;
  color: white;
  cursor: pointer;
}
.about>h1{
  text-align: center;
  margin-bottom: 50px;
}
.upvote{
  display: grid;
  grid-template-columns: 100px auto;
}
.upvote>button{
  padding: 5px 5px;
  margin-left: 0;
}
.upvote>p{
  margin: 10px 30px;
}
.comments{
  margin-left: 5%;
}
@media screen and (max-width: 600px) {
  body{
    font-size: 15px;
  }
}